<template>
  <div>
    <!-- 管理员账号 -->
    <v-card class="linkman">
      <v-data-table hide-default-footer
                    :headers="headers"
                    :items="desserts"
                    :items-per-page="5"
                    class="elevation-1">
        <template v-slot:item.org_level_name="{ item }">
          <div class="d-flex flex-column">
            <span v-for="(item1,index1) in item.org_level_name"
                  :key="index1">
              {{item1}}
            </span>
          </div>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template> 

<style scoped>
.linkman {
  padding: 40px 40px;
}
.company {
  text-align: left;
}
</style>
 
<script>
export default {
  props: ['list']
  ,
  data () {
    return {
      headers: [
        {
          text: '账号',
          align: 'start',
          value: 'user_account',
          sortable: false
        },
        {
          text: '管理员类型',
          value: 'manager_type',
          align: 'start',
          sortable: false
        },
        {
          text: '关联单位',
          value: 'org_level_name',
          align: 'start',
          sortable: false
        }
      ],
      desserts: [],
    }
  },
  computed: {
    mrbase_sys_org_manager () {
      return this.list && this.list.map(item => {
        const obj1 = {}
        if (item.manager_type === 4) {
          obj1['manager_type'] = '类型4'
        }
        if (item.manager_type === 3) {
          obj1['manager_type'] = '类型3'
        }
        // console.log(item.org_level_name += ',1,2,3');
        obj1['org_level_name'] = (String(item.org_level_name).indexOf(',') != -1) ? item.org_level_name.split(',') : [item.org_level_name];
        obj1['user_account'] = item.user_account
        obj1['user_pwd'] = item.user_pwd
        return obj1
      })
    }
  },
  mounted () {
    this.desserts = this.mrbase_sys_org_manager
  },
  watch: {
    list: {
      handler (val) {
        this.desserts = this.mrbase_sys_org_manager
      }, deep: true
    }
  }

}
</script>